body{margin:0;font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}html,body,#root{height:100%;width:100%;background:#e7ebe0;color:#0e527b;font-weight:bold}.container{display:flex;flex-grow:1;padding:0 5%}@media (min-width: 768px){.container{padding:0 10%}}@media (min-width: 1200px){.container{padding:0 15%}}:focus{outline:none}::-moz-focus-inner{border:0}

.HeadingText{display:flex;justify-content:center;margin:0.5em auto}

.Cart{display:flex;flex-direction:column;align-items:center;justify-content:space-between;font-size:1.5em;width:100%}.cart_products{display:flex;flex-direction:column;width:100%}.cart_product{display:grid;grid-template-columns:repeat(2, minmax(100px, 1fr));grid-row-gap:1.5em;row-gap:1.5em;margin:1em 0;align-items:center}@media (min-width: 1200px){.cart_product{grid-template-columns:repeat(4, minmax(200px, 1fr))}}.empty{flex-basis:50%}.grid_item{display:flex}.grid_item:nth-child(odd){justify-content:flex-start}.grid_item:nth-child(even){justify-content:flex-end}@media (min-width: 768px){.grid_item:nth-child(3){justify-content:flex-end}}.cart_btn{height:3em;width:6em;font-weight:bold;font-size:1em;border:4px solid #0e527b;border-radius:8px;cursor:pointer;background:#bfc3c7;transition:all 0.2s ease-in-out;margin-bottom:2em}.cart_btn:hover{color:#bfc3c7;background:#0e527b;border-color:#bfc3c7}.cart_btn a{color:#0e527b;height:100%}.cart_btn:hover a{color:#bfc3c7}.btn-group{border-radius:4px;display:flex;justify-content:center;align-items:center}.btn-group button{background-color:#bfc3c7;border:2px solid #0e527b;color:#e7ebe0;cursor:pointer;float:left;padding:0.5em;width:3em;font-size:0.725em}.btn-group button:disabled{opacity:0.5;pointer-events:none}.btn-group button:not(:last-child){border-right:none}.btn-group button:first-child{border-bottom-left-radius:4px;border-top-left-radius:4px}.btn-group button:last-child{border-bottom-right-radius:4px;border-top-right-radius:4px}

.Order{display:flex;flex-direction:column;width:100%;align-items:center;font-size:1.5em}.Order .message{display:flex;align-items:center;text-align:center;flex-basis:50%}.Order form{display:flex;flex-direction:column;justify-content:space-evenly;flex-grow:1}@media (min-width: 768px){.Order form{width:80%}}.Order .flex_item{display:flex;justify-content:space-between}.Order .flex_item:last-child{justify-content:center}.Order button{height:3em;width:6em;font-weight:bold;font-size:1em;border:4px solid #0e527b;border-radius:8px;cursor:pointer;background:#bfc3c7;transition:all 0.2s ease-in-out}.Order button:hover{color:#bfc3c7;background:#0e527b;border-color:#bfc3c7}

.ProductCard{display:flex;flex-direction:column;position:relative;align-items:center;box-shadow:1px 2px 4px #6bb1be;border-radius:4px;width:40vh;height:40vh;margin:1em auto;transition:all 0.2s ease-in-out}.ProductCard:hover{box-shadow:2px 4px 8px #6bb1be;transform:scale(1.05)}@media (min-width: 768px){.ProductCard{width:25vw;height:25vw}}@media (min-width: 1200px){.ProductCard{width:15vw;height:15vw}}.ProductCard img{width:100%;background:#0e527b;min-height:70%;object-fit:cover}.ProductCard .buy_button{display:flex;width:100%;height:100%;letter-spacing:6px;overflow:hidden;white-space:nowrap}.ProductCard .buy_button__item{flex-basis:50%;font-size:2vh;font-weight:bold;display:flex;justify-content:center;align-items:center}@media (min-width: 1200px){.ProductCard .buy_button__item{font-size:1.2vw}}.ProductCard .buy_button__item:nth-child(1){border-bottom-left-radius:4px}.ProductCard .buy_button__item:nth-child(2){border-bottom-right-radius:4px}.ProductCard .buy_button button{background:#0e527b;color:#bfc3c7;border:none;cursor:pointer}

.Homepage{width:100%;display:grid;grid-template-areas:"catalog" "product" "pager"}@media (min-width: 768px){.Homepage{grid-template-areas:"catalog product" "catalog pager";grid-template-columns:1fr 3fr;grid-template-rows:1fr}}.product_gallery{grid-area:product;display:flex;flex-flow:row wrap;justify-content:space-evenly}

@media (min-width: 1200px){.Catalog{grid-area:catalog;display:flex;align-self:flex-start;position:-webkit-sticky;position:sticky;top:5em}}.Catalog ul{width:100%;padding:0}.Catalog li{display:flex;flex-direction:column}.Catalog li .sub{width:100%}.Catalog img{opacity:0.5;z-index:-1}.Catalog .active{background:#0e527b;color:#bfc3c7}.Catalog .sub{height:0}.Catalog .sub__show{height:auto}.Catalog button{border:none;height:2em;font-weight:bold;cursor:pointer;transition:all 0.2s ease-in-out}.Catalog button:hover{color:#bfc3c7;background:#0e527b;border-color:#bfc3c7}

.Pager{grid-area:pager;display:flex;justify-content:center;align-items:center;margin:1em}.Pager .arrow_btn{display:flex;background-color:#bfc3c7;cursor:pointer;border-radius:14px;border:2px solid #6bb1be;transition:all 0.2s ease-in-out}.Pager .arrow_btn:hover{transform:scale(1.05)}.Pager .arrow_btn:disabled{opacity:0.5;pointer-events:none}.Pager svg{height:2em}.Pager svg path{fill:#0e527b}.Pager .page_number{font-size:1.5em;margin:0 1em}

.Header{position:-webkit-sticky;position:sticky;top:0;background:#0e527b;color:#bfc3c7;z-index:1000}.Header a{color:#bfc3c7}nav{display:flex;justify-content:space-between;align-items:center;border-bottom:4px solid #bfc3c7}

.CartWidget{display:flex;flex-direction:column;align-items:center;position:relative;transition:all 0.2s ease-in-out;min-height:5em;width:5em}.CartWidget img{position:absolute;top:15%;opacity:0.5;height:75%;-webkit-filter:invert(1) opacity(50%);filter:invert(1) opacity(50%)}.CartWidget .text{z-index:100;font-size:1.5em}.CartWidget:hover{transform:scale(1.1)}@media (min-width: 1200px){.CartWidget{margin:0 1.5em}}

.App{height:100%;display:flex;flex-direction:column;overflow:auto}:-webkit-any-link{color:#0e527b;text-decoration:none}:any-link{color:#0e527b;text-decoration:none}

